body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: #252525;
}

a {
  text-decoration: none;
  color: #252525;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 10px;
}

.btn {
  text-transform: uppercase;
  margin: 35px auto;
  padding: 10px;
  border: 1px solid rgba(0,0,0,.29) !important;
  cursor: pointer;
  background: #fff;
  font-size: 16px;
  width: 250px;
  position: relative;
}

.btn:hover {
  border-color: rgba(0,0,0,.5);
  text-decoration: none;
}

.btn:focus {
  outline: 0;
  font-weight: 700;
  border-width: 2px;
}

.center {
  text-align: center;
}

.active {
  font-weight: bold;
}

.nav ul {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  text-decoration: none;
}

.nav li:first-child {
  padding-left: 0;
}

ul {
  padding-left: 0;
}

li {
  list-style-type: none;
  padding: 10px;
  text-decoration: none;
}

.avatar {
  height: 50px;
  border-radius: 25px;
  margin: 10px;
}

.question {
  width: 100%;
  max-width: 590px;
  padding: 10px;
  border: 1px solid #dad7d7;
  border-radius: 3px;
  margin: 0 auto;
}

.question-info {
  margin: 10px;
  display: flex;
  flex-direction: row;
  cursor: pointer;
}

.vote-form {
  margin: 1rem 0;
}

.form-check-label {
  padding-left: .45rem;
  color: #252525 !important;
}

.form-check-input.is-valid:checked {
  background-color: #252525 !important;
}

.new-question {
  width: 100%;
  max-width: 590px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.chart {
	width: auto;
	/* margin: 25px 25px 25px 400px;
	height: 600px; */
	border-bottom: 1px solid;
	border-left: 1px solid;
}

.option {
	width: 100%;
	height: auto;
	float: left;
	clear: both;
	text-align: left;
	margin: 12px 0;
}
.option-label {
	position: absolute;
	left: 0;
	width: 380px;
	height: 52px;
	margin: 38px 20px 38px 0;
	font-size: 44px;
	text-align: right;
}
.results {
	height: 120px;
	width: 100%;
	margin: 5px 0 0;	
}
.results .on {
	height: 120px;
	opacity: 0.75;
	width: 0;
}
.results .on .count {
	line-height: 120px;
	font-size: 60px;
	vertical-align: middle;
	text-align: left;
	position: relative;
	left: 81%;
	display: inline-block;
	text-shadow: 2px 2px 1px rgba(255,255,255,0.75);
}


#option-1 .results .on {
  width: 20%;
}

.option-label, .results .on .count {
  color: #0f79e0;
}

.chart {
  border-color: #83bbf1;
}

.results {
  background-color: rgba(87, 163, 236, 0.5);
}

.results .on {
  background-color: #57a3ec;
}
